.main-title {
  @include font(700, 27px, 30px);
  color: $color-text-title;
  background-color: $color-bg-darken;
  min-height: 177px;
  padding-top: 73px;
  position: relative;

  &--index {
    background-image: url("../img/hero-mobile2x1.jpg");
    background-repeat: no-repeat;
    background-position: center -17px;
    background-color: #f9f9f9;
    min-height: 490px;
    padding: 73px 0 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.main-title__text {
  @include font(700, 27px, 30px);
  color: $color-text-title;
  text-align: center;
  margin: 0 auto;
  padding: 0 30px;
  width: $mobile-width;
}

@media (min-width: $tablet-width) {
  .main-title__text {
    @include font(700, 43px, 50px);
    width: $tablet-width;
  }

  .main-title {
    min-height: 238px;
    padding-top: 101px;
    margin-top: -88px;

    &--index {
      min-height: 746px;
      background-image: url("../img/hero-desktop.jpg");
      background-position: bottom;
      padding-top: 155px;
    }

    &--index::after {
      content: "";
      position: absolute;
      width: 770px;
      height: 24px;
      background-image: url("../img/zigzag-fill-tablet.svg");
      background-repeat: no-repeat;
      bottom: -12px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}

@media (min-width: $desktop-width) {
  .main-title__text {
    @include font(700, 53px, 57px);
    width: $desktop-width;
  }

  .main-title {
    min-height: 272px;

    &--index {
      min-height: 763px;
      padding: 158px 0 17px;
      background-position: top;
    }

    &--index::after {
      background-image: url("../img/zigzag-fill-desktop.svg");
      width: 1050px;
      height: 34px;
      bottom: 0;
    }
  }
}
